.footer {
    display: flex;
    justify-content: center;
    padding: 2rem;
    font-size: 0.8rem;
    color: var(--tesla-gray);
    margin-top: auto;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.links {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
}

.links a {
    transition: color 0.3s ease;
}

.links a:hover {
    text-decoration: underline;
    color: var(--tesla-light);
}